.banner {
  position: relative;
  padding-bottom: 260px;
  padding-top: 120px;
  text-align: center;
  font-size: 34px;
  font-weight: 300;
  color: rgba(#fff, .9);
  background-color: $c-bg-primary;

  &__logo {
    width: 300px;
    height: 300px;
    display: inline-block;
    margin-left: -25px; }

  &__brand-text {
    display: inline-block;
    vertical-align: top;
    padding-top: 1em;
    margin-left: -.33em; }

  &__title {
    font-size: 96px;
    font-weight: 400;
    color: #fff; }

  &__subtitle {
    display: block;
    text-align: right; }

  &__nets {
    margin-top: 30px; }

  @media all and (max-width: 1366px) {
    padding-top: 90px; }

  @media all and (max-width: 1024px) {
    min-height: 100vh;
    font-size: 14px;

    &__logo {
      width: 115px;
      height: 115px; }

    &__title {
      font-size: 48px; } } }
