.fab {
  visibility: visible;
  border: none;
  text-decoration: none;
  z-index: 100;
  display: flex;
  padding: 16px;
  right: 56px;
  width: 56px;
  height: 56px;
  border-radius: 100%;
  background-color: $c-primary;
  box-shadow: 0 14px 14px rgba(0, 0, 0, 0.237602), 0 0 14px rgba(0, 0, 0, 0.12), 0 8px 8px rgba(0, 0, 0, 0.24), 0 0 8px rgba(0, 0, 0, 0.12);

  .svg {
    fill: #212121; }

  &._share {
    position: absolute;
    bottom: 0;
    transform: translateY(50%); }

  &._more {
    display: none;
    position: fixed;
    bottom: 25px; }

  @media all and (max-width: 1024px) {
    right: 15px;
    &._share {
      position: fixed;
      bottom: 25px;
      transform: none; }
    &._more {
      display: flex; } } }
